.raw-json {
	margin: 10px 0;

	&__toggle {
		display: none;

		&:checked + &-label {
			&::after {
				content: '−';
				transform: rotate(0deg);
			}
		}

		&:checked ~ .raw-json__json {
			display: block;
		}

		&-label {
			border: 1px solid black;
			border-radius: 5px;
			display: inline-block;
			padding: 10px;
			width: auto;

			&-text {
				display: inline-block;
				vertical-align: middle;
			}

			&::after {
				content: '+';
				display: inline-block;
				font-size: 1.25em;
				line-height: 0; 
				margin-left: 8px;
				text-align: center;
				transition: all 0.3s ease;
				transform: rotate(-90deg);
				vertical-align: middle;
				width: 1em;
			}
		}
	}	
	
	&__json {
		background-color: rgb(247, 247, 247);
		border: 1px solid rgba(0,0,0,0.05);
		border-radius: 5px;
		display: none;
		padding: 20px;
		white-space: pre-wrap;
		word-break: break-word;
		
		* {
			display: inline;
			margin: 0;
			padding: 0;		
		}
	}

	&__xmp {
		white-space: pre-wrap;
		word-break: break-word;
	}
}
